import { Component } from '@angular/core';
import { IonicPage, ModalController, NavController, NavParams } from 'ionic-angular';
import { HospitalMonitoringProvider } from '../../providers/hospital-monitoring/hospital-monitoring';
import { PatientMonitoringMapper } from '../../models/patient-monitoring.model';
import { PatientHospitalMonitoringPage } from '../index';

/**
 * Generated class for the HospitalMonitoringPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage({
  name: 'HospitalMonitoringPage'
})
@Component({
  selector: 'page-hospital-monitoring',
  templateUrl: 'hospital-monitoring.html',
})
export class HospitalMonitoringPage {

  public currentItems: PatientMonitoringMapper[] = [];

  constructor(public navCtrl: NavController, public navParams: NavParams, private modalCtrl: ModalController,
              private hospitalMonitoring: HospitalMonitoringProvider) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad HospitalMonitoringPage');
    this.getItems();
  }

  getItems() {
    this.hospitalMonitoring.patientHospitalized().subscribe((data: any) => {
      this.currentItems = data;
    });
  }

  detailMonitoring(monitoring) {

    console.log('detail.monitoring', monitoring);
    let patientHospitalMonitoringPage =
      this.modalCtrl.create(PatientHospitalMonitoringPage, {monitoring: monitoring});

    patientHospitalMonitoringPage.onDidDismiss((data: any) => {
      if (data.reload) {
        this.getItems();
      }
    });

    patientHospitalMonitoringPage.present();
  }

  doRefresh(refresher) {
    this.hospitalMonitoring.patientHospitalized().subscribe((data: any) => {
      this.currentItems = data;
      refresher.complete();
    });
  }

}



